import tkiQrcode from '@/components/tki-qrcode/tki-qrcode.vue'

export default {
	props: ['goodsInfo', 'money', 'kanId', 'child'],
	data() {
		return {
			ifShow: true,
			val: '', // 要生成的二维码值
			size: 100, // 二维码大小
			unit: 'upx', // 单位
			pdground: '#FF625E', // 角标色
			icon: '', // 二维码图标
			iconsize: 15, // 二维码图标大小
			lv: 3, // 二维码容错级别 ， 一般不用设置，默认就行
			onval: false, // val值变化时自动重新生成二维码
			loadMake: false, // 组件加载完成后自动生成二维码
			src: '' // 二维码生成后的图片地址或base64
		}
	},
	mounted() {
		var a = {
			k: this.kanId,
			g: this.child
		}
		a = JSON.stringify(a)
		this.val = a;
		setTimeout(() => {
			this.goodsInfo.old = this.money;
			this.goodsInfo.key = true;
			this.$refs.qrcode._makeCode() //生成二维码
		}, 1000)
	},
	components: {
		tkiQrcode
	},
	methods: {

		qrR(res) {
			this.src = res
		},
		saveQrcode() {

			this.$refs.qrcode._saveCode()
			setTimeout(() => {
				uni.showToast({
					icon: "success",
					title: "保存成功,打开APP识别",
				})
			}, 200)
		},
		longtap() { //长按保存
			uni.showModal({
				title: '保存二维码',
				content: '保存到本地相册吗？',
				cancelText: '否',
				confirmText: '是',
				success: res => {
					if (res.confirm) {
						this.saveQrcode()
					}
				}
			})
		},

		// 执行事件 //分享到微信好友
		formSubmit: function() {




















		},
		//分享到朋友圈
		formSubmit2: function() {
			uni.share({
				provider: "weixin",
				scene: "WXSenceTimeline",
				type: 0,
				href: "http://uniapp.dcloud.io/goods=" + JSON.stringify(this.goodsInfo),
				title: "快来帮朋友砍一刀吧~",
				summary: this.goodsInfo.name,
				imageUrl: "/static/img/home/" + this.goodsInfo.img,
				success: function(res) {
					console.log("success:" + JSON.stringify(res));
				},
				fail: function(err) {
					console.log("fail:" + JSON.stringify(err));
				}
			});
		},
	}
}
